//Menu wrapper
.menu-list {
  width: 224px;
  .menu-item + .menu-item {
    margin-left: 0px;
  }
  .menu-item {
    justify-content: flex-start;
    padding: 11px 16px;
  }
}
.menu-popper {
  padding-bottom: 8px;
}

.menu-body {
  overflow-y: auto;
}
//Menu Items

.menu-item {
  width: 100%;
  justify-content: flex-start;
  border-radius: 0;
  line-height: 1.8rem;
  font-weight: 500;
  color: var(--text-color);
  &:hover {
    background-color: rgba(22, 24, 35, 0.03);
  }
  &.separate {
    border-top: 1px solid rgba(22, 24, 35, 0.12);
  }
}

//header

.header {
  position: relative;
  height: 50px;
  margin-top: -8px;
  flex-shrink: 0;
}
.back-btn {
  width: 50px;
  height: 100%;
  cursor: pointer;
  background-color: transparent;
}
.header-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
