.wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 100px;
  padding: 9px 16px;
  border-radius: 4px;
  font-size: 1.6rem;
  font-weight: 700;
  font-family: var(--font-family);
  cursor: pointer;
  background-color: var(--white);
  border: 1px solid transparent;
  user-select: none;

  + .wrapper {
    margin-left: 8px;
  }
  &.disabled {
    pointer-events: none;
    opacity: 0.5;
  }
}
.icon + .tittle,
.tittle + .icon {
  margin-left: 8px;
}
.icon {
  display: inline-block;
  width: 24px;
  text-align: center;
}
//Button type
.rounded {
  border-radius: 999px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
  border-color: rgba(22, 24, 35, 0.12);
  &:hover {
    border-color: rgba(22, 24, 35, 0.2);
    background-color: rgba(22, 24, 35, 0.03);
  }
}
.primary {
  background-color: var(--primary);
  border-color: var(--primary);
  color: var(--white);
  &:hover {
    border-color: var(--primary);
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.06), rgba(0, 0, 0, 0.06)),
      #fe2c55;
  }
}
.outline {
  background-color: var(--white);
  color: var(--primary);
  border-color: var(--primary);
  &:hover {
    background-color: rgba(254, 44, 85, 0.06);
    border-color: var(--primary);
  }
}
.text {
  &:hover {
    text-decoration: underline;
  }
}
//Button size

.small {
  min-width: 88px;
  padding: 4px 16px;
}

.large {
  min-width: 140px;
  padding: 14px 16px;
}
